import React, { useState, useEffect } from "react";
import { Row, Col, Card, Container, CardGroup } from "react-bootstrap";
import web3_static from "../../styles/assets/images/web3_development/web3_static.svg";
import web3_first from "../../styles/assets/images/web3_development/web3_first.svg";
import web3_second from "../../styles/assets/images/web3_development/web3_second.svg";
import web3_third from "../../styles/assets/images/web3_development/web3_third.svg";
import web3_fourth from "../../styles/assets/images/web3_development/web3_fourth.svg";
import web3_invest from "../../styles/assets/images/web3_development/web3_invest.svg";
import web3_animated_image from "../../styles/assets/images/web3_development/web3_invest_animated_image.svg";
import web3_decode from "../../styles/assets/images/web3_development/web3_decode.svg";
import web3_invest_hover from "../../styles/assets/images/web3_development/web3_invest_hover_image.svg";
import web3_offers_image from "../../styles/assets/images/web3_development/web3_service_offer_image.svg";

const Web3DevService = () => {
  const [imageSources, setImageSources] = useState([
    web3_static,
    web3_first,
    web3_second,
    web3_third,
    web3_fourth,
  ]);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const imageGalleryStyle = {
    width: "500px",
    backgroundColor: "transparent",
    overflow: "hidden",
  };

  const imageStyle = {
    width: "100%",
    maxWidth: "500px",
    opacity: 0,
    position: "absolute",
    transition: "opacity 1000ms ease-out",
  };

  const activeImageStyle = {
    opacity: 1,
    animation: "fadeIn 1s",
  };

  useEffect(() => {
    const imageChangeInterval = setInterval(() => {
      setCurrentImageIndex(
        (prevIndex) => (prevIndex + 1) % imageSources.length
      );
    }, 3000);

    return () => clearInterval(imageChangeInterval);
  }, [imageSources]);

  const [isHovered, setIsHovered] = useState(false);

  const handleMouseOver = () => {
    setIsHovered(true);
  };

  const handleMouseOut = () => {
    setIsHovered(false);
  };

  const getImageSource = () => {
    return isHovered ? web3_invest_hover : web3_decode;
  };

  const investImageSource = () => {
    return isHovered ? web3_animated_image : web3_invest;
  };

  return (
    <div className="web3_service_wrapper">
      <section>
        <div className="web3_service">
          <div className="web3_service_hero_content">
            <div className="container">
              <Row>
                <Col xl={7} lg={6} md={12} sm={12} xs={12}>
                  <div className="web3_service_hero_heading">
                    <div className="web3_service_hero_heading_content">
                      <h1>
                        Web3 Smart Contract Development
                        <br /> Company
                      </h1>
                      <p>
                        Team up with Web3 visionaries to get hold of a
                        cutting-edge smart contract. Seize the future today with
                        Justtry's all-inclusive web3 smart contract development
                        services.
                      </p>
                    </div>
                    <div>
                      <button className="web3_custom_btn">TALK TO US</button>
                    </div>
                  </div>
                </Col>
                <Col
                  xl={5}
                  lg={6}
                  md={12}
                  sm={12}
                  xs={12}
                  className="web3_service_hero_img_wrapper"
                >
                  <div
                    style={imageGalleryStyle}
                    className="web3_service_hero_image_gallery"
                  >
                    {imageSources.map((src, index) => (
                      <img
                        key={src}
                        src={src}
                        alt={`Image ${index + 1}`}
                        className={`image ${
                          index === currentImageIndex ? "active" : "inactive"
                        }`}
                        style={{
                          ...imageStyle,
                          ...(index === currentImageIndex
                            ? activeImageStyle
                            : {}),
                        }}
                      />
                    ))}
                  </div>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </section>
      <section>
        <div className="container web3_service_intro_wrapper">
          <Row className="custom_margin_top">
            <Col
              md={12}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="web3_service_intro">
                <h2>
                  Web3 Smart Contract Development: Paving the &nbsp;
                  <span className="web3_service_intro_span">
                    Way for Tomorrow
                  </span>
                </h2>
                <p>
                  According to recent market research, the global smart contract
                  market is anticipated to surge to a staggering USD 9,850
                  million by 2030. This value was around USD 1,750 million in
                  2022. There has been substantial growth in smart contact’s
                  market value, and as projected, it will grow at an impeccable
                  rate. But what exactly is a smart contract? IBM defines smart
                  contracts as programs stored on Blockchain that get executed
                  when predefined conditions are satisfied.
                </p>
                <p>
                  Smart contracts automate the execution of agreements without
                  the involvement of intermediaries. Additionally, they can
                  effortlessly streamline the workflow by automatically
                  initiating the next step once the specified conditions are
                  met. Countless startups and organizations are eagerly seeking
                  impeccable smart contracts by joining hands with firms
                  delivering Web3 smart contract development services.
                </p>
              </div>
            </Col>
          </Row>
          <svg
            className="web3_intro_svg_right"
            xmlns="http://www.w3.org/2000/svg"
            width="234"
            height="307"
            viewBox="0 0 234 307"
            fill="none"
          >
            <g filter="url(#filter0_f_660_5454)">
              <circle
                cx="250"
                cy="250"
                r="195"
                stroke="#2D41D4"
                strokeWidth="10"
              />
            </g>
            <defs>
              <filter
                id="filter0_f_660_5454"
                x="0"
                y="0"
                width="500"
                height="500"
                filterUnits="userSpaceOnUse"
                color-interpolation-filters="sRGB"
              >
                <feFlood flood-opacity="0" result="BackgroundImageFix" />
                <feBlend
                  mode="normal"
                  in="SourceGraphic"
                  in2="BackgroundImageFix"
                  result="shape"
                />
                <feGaussianBlur
                  stdDeviation="25"
                  result="effect1_foregroundBlur_660_5454"
                />
              </filter>
            </defs>
          </svg>

          <svg
            className="web3_intro_svg_left"
            xmlns="http://www.w3.org/2000/svg"
            width="235"
            height="258"
            viewBox="0 0 235 258"
            fill="none"
          >
            <g filter="url(#filter0_f_660_5452)">
              <circle cy="235" r="195" stroke="#7534FC" strokeWidth="10" />
            </g>
            <defs>
              <filter
                id="filter0_f_660_5452"
                x="-235"
                y="0"
                width="470"
                height="470"
                filterUnits="userSpaceOnUse"
                color-interpolation-filters="sRGB"
              >
                <feFlood flood-opacity="0" result="BackgroundImageFix" />
                <feBlend
                  mode="normal"
                  in="SourceGraphic"
                  in2="BackgroundImageFix"
                  result="shape"
                />
                <feGaussianBlur
                  stdDeviation="17.5"
                  result="effect1_foregroundBlur_660_5452"
                />
              </filter>
            </defs>
          </svg>
        </div>
      </section>
      <section onMouseOver={handleMouseOver} onMouseOut={handleMouseOut}>
        <div className="container">
          <Row className="custom_margin_top web3_service_animated_image">
            <Col
              lg={6}
              md={12}
              className="d-flex align-content-center justify-content-center"
            >
              <div className="web3_service_sec_invest">
                <h2 className="text-start">
                  Why Should You Invest in Smart Contract Development?
                </h2>
                <p>
                  Smart contracts are considered the ‘backbone of blockchain
                  technology’. Without these Web3 smart contracts, we will be
                  stuck in a centralized world with no control over our data.
                  From healthcare to supply chains, blockchain has made its mark
                  in every possible sector thanks to its all-powerful smart
                  contracts. Since smart contracts are a vital part of this Web3
                  era, it is crucial to align with smart contract developers who
                  have proven expertise in delivering flawless smart contracts.
                  Fortunately for you, Justtry boasts an in-house team of smart
                  contract developers with a proven track record of successfully
                  delivering numerous smart contracts to clients worldwide.
                  Let’s get started today!
                </p>
              </div>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="web3_service_sec_invest_img">
                <img
                  className="web3_invest_img"
                  src={investImageSource()}
                  onMouseOver={handleMouseOver}
                  onMouseOut={handleMouseOut}
                  alt="web3_service_Smart_investment_image"
                />
              </div>
            </Col>
          </Row>
        </div>
      </section>
      <section>
        <div className="container">
          <Row className="custom_margin_top">
            <Col md={12}>
              <div className="web3_service_talk_to_us_wrapper">
                <div className="web3_service_talk_to_us">
                  <h3 className="text-white text-center">
                    Unlock Tomorrow with Our Comprehensive Web3 Smart Contract
                    Development Services Right Away!
                  </h3>
                  <div className="button_wrapper">
                    <button className="moving_button">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="34"
                        height="22"
                        viewBox="0 0 34 22"
                        fill="none"
                      >
                        <path
                          id="Vector"
                          d="M23.2474 1L33.2437 11.0012L23.2474 21M0.755859 11.0037H33.2437"
                          stroke="black"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                    </button>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </div>
      </section>
      <section>
        <div className="container">
          <Row className="custom_margin_top ">
            <Col lg={8} md={12}>
              <div className="web3_service_decode_content">
                <h2 className="text-start">
                  Decoding Smart Contracts: How Do They Work?
                </h2>
                <p>
                  Smart contracts operate using basic “if-then” conditions
                  written into code residing on the blockchain. A group of
                  connected computers performs the required actions when preset
                  conditions are met and verified. These actions can involve
                  tasks like transferring funds, recording sale data, sending
                  alerts, or generating reports. The blockchain is updated upon
                  transaction completion, ensuring that it remains unalterable,
                  with results visible only to authorized parties. To set the
                  conditions, users need to decide how transactions and their
                  information will be stored on the blockchain, agree on the
                  rules that control those transactions, consider any potential
                  problems, and create a way to handle disputes. Once everything
                  is confirmed, the developer starts building the smart contract
                  by transforming the agreement into code.
                </p>
              </div>
            </Col>
            <Col
              lg={4}
              md={12}
              className="d-flex align-items-center justify-content-center"
            >
              <div className="web3_service_decode_img_wrapper">
                <img
                  className="web3_service_decode_img"
                  src={getImageSource()}
                  onMouseOver={handleMouseOver}
                  onMouseOut={handleMouseOut}
                  alt="web3_decode_vector_image"
                />
              </div>
            </Col>
          </Row>
        </div>
      </section>
      <section>
        <div className="container">
          <Row className="custom_margin_top ">
            <Col lg={12}>
              <h2 style={{ marginBottom: "20px" }}>
                Lead the Way With Our Innovative Smart Contract <br />
                Development Services
              </h2>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex justify-content-center align-items-center"
            >
              <div className="web3_service_smart_card_wrapper">
                <Card>
                  <Card.Body>
                    <Card.Title>Smart Contract Consultation Service</Card.Title>
                    <Card.Text>
                      Our blockchain consultants are the experts you can rely on
                      to help you make the most of smart contracts. They offer
                      valuable insights into the design and development of
                      faultless smart contracts. They will also suggest the best
                      blockchain platform that fulfills your project
                      requirements.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </div>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex justify-content-center align-items-center"
            >
              <div className="web3_service_smart_card_wrapper">
                <Card>
                  <Card.Body>
                    <Card.Title>Smart Contract Upgradation</Card.Title>
                    <Card.Text>
                      Through smart contract upgradation, we assist you in
                      reducing gas fees, enhancing token performance, and
                      bolstering the security of your private keys—resulting in
                      a cost-efficient and reliable smart contract.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </div>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex justify-content-center align-items-center"
            >
              <div className="web3_service_smart_card_wrapper">
                <Card>
                  <Card.Body>
                    <Card.Title>Custom Smart Contract Development</Card.Title>
                    <Card.Text>
                      We specialize in crafting tailored, impeccable smart
                      contracts for various industries, aligning them with
                      specific needs on a diverse spectrum of public, private,
                      and hybrid blockchains.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </div>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex justify-content-center align-items-center"
            >
              <div className="web3_service_smart_card_wrapper">
                <Card>
                  <Card.Body>
                    <Card.Title>Smart Contract Audit Service</Card.Title>
                    <Card.Text>
                      In our capacity as a smart contract development firm, we
                      extend smart contract audit services aimed at uncovering
                      glitches and security weaknesses, thereby guaranteeing the
                      overall resilience of your application.
                    </Card.Text>
                  </Card.Body>
                </Card>
              </div>
            </Col>
          </Row>
        </div>
      </section>

      <section>
        <Container>
          <Row className="custom_margin_top">
            <Col>
              <div className="web3_service_flex_center_div">
                <h2 className="text-center">
                  Our Innovative Web3 Smart Contract
                  <br /> Development Process
                </h2>
                <p className="text-center">
                  Creating a smart contract involves seamless planning, and
                  demands a strategic approach. With a clear understanding of
                  your goals, we initiate the smart contract development
                  process, comprised of the following key steps:
                </p>
              </div>
            </Col>
          </Row>
          <Row>
            <Col>
              <div className="web3_service_innovative_cardgroup">
                <CardGroup>
                  <Card>
                    <Card.Body>
                      <Card.Title>Smart Contract Design</Card.Title>
                      <Card.Text>
                        Our developers meticulously evaluate the triggering
                        events that will initiate the smart contract’s
                        deployment. They examine potential factors that could
                        impact the smart contract's operations. Furthermore, we
                        identify vulnerabilities in the decentralized platform
                        where the contract will operate. This aids in developing
                        a tailored smart contract that acts as the pillar of
                        your platform.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                  <Card>
                    <Card.Body>
                      <Card.Title>Planning and Research</Card.Title>
                      <Card.Text>
                        We define the scope and identify the primary
                        stakeholders. Our expert team delves into market
                        research to assess if your smart contract addresses a
                        real-world problem. This is essential to determining
                        your smart contract’s success in the long-run.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                  <Card>
                    <Card.Body>
                      <Card.Title>Smart Contract Development</Card.Title>
                      <Card.Text>
                        We establish the optimal development environment and
                        kickstart the coding process once the smart contract
                        design is in place. Our team employs the right
                        programming languages and frameworks to build your smart
                        contract.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                </CardGroup>
              </div>
              <div className="web3_service_innovative_cardgroup">
                <CardGroup>
                  <Card>
                    <Card.Body>
                      <Card.Title>Testing Phase</Card.Title>
                      <Card.Text>
                        Testing is a vital step we take post-development to
                        ensure the smart contract's integrity. This process
                        helps us identify and rectify any coding errors before
                        deploying the agreement. We employ cutting-edge tools
                        and techniques for thorough testing of the contract.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                  <Card>
                    <Card.Body>
                      <Card.Title>Deployment Phase</Card.Title>
                      <Card.Text>
                        After completing the testing phase and ensuring your
                        smart contract is ready, we proceed with deployment. The
                        overall deployment process varies depending on the
                        underlying blockchain.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                  <Card>
                    <Card.Body>
                      <Card.Title>Smart Contract Audit</Card.Title>
                      <Card.Text>
                        Our team conducts a comprehensive audit of your
                        contract's execution to confirm its proper functioning.
                        Justtry’s experienced smart contract auditors
                        meticulously assess your code to ensure its
                        functionality. Following the audit, we provide a
                        detailed report highlighting any identified issues that
                        need addressing.
                      </Card.Text>
                    </Card.Body>
                  </Card>
                </CardGroup>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <section>
        <div className="container">
          <Row className="custom_margin_top">
            <Col lg={6} md={12}>
              <div className="web3_service_contract">
                <h2>Why Choose Justtry for Web3 Smart Contract Development?</h2>
              </div>
            </Col>
            <Col
              lg={6}
              md={12}
              className="d-flex align-items-center justify-content-center pt-md-5"
            >
              <div className="web3_service_join_content">
                <p className="text-center">
                  Since its establishment, Justtry Technologies has had the
                  honor of collaborating with clients across diverse industry
                  sectors. Throughout the years, we have harnessed our
                  development expertise to create cutting-edge and
                  cost-effective business solutions for our valued customers.
                  Regardless of the complexity or scale of your business needs,
                  our team of web3 experts is ever-prepared to serve you. Join
                  Forces with Us Today!
                </p>
              </div>
            </Col>
          </Row>
        </div>
      </section>
      <section className="web3_service_offers_bg">
        <div className="container">
          <Row className="mb-5">
            <Col lg={7} md={12}>
              <div className="web3_service_sec_offers_wrapper">
                <div>
                  <h2 className="text-center text-white">We offers</h2>
                </div>
                <div className="web3_service_sec_offers">
                  <svg
                    width="35"
                    height="36"
                    viewBox="0 0 35 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M0 18C0 13.3587 1.84374 8.90752 5.12563 5.62563C8.40752 2.34374 12.8587 0.5 17.5 0.5C22.1413 0.5 26.5925 2.34374 29.8744 5.62563C33.1563 8.90752 35 13.3587 35 18C35 22.6413 33.1563 27.0925 29.8744 30.3744C26.5925 33.6563 22.1413 35.5 17.5 35.5C12.8587 35.5 8.40752 33.6563 5.12563 30.3744C1.84374 27.0925 0 22.6413 0 18ZM16.5013 25.49L26.5767 12.8947L24.7567 11.4387L16.1653 22.1743L10.08 17.104L8.58667 18.896L16.5013 25.4923V25.49Z"
                      fill="#00C265"
                    />
                  </svg>
                  <h3>Unparalleled Proficiency</h3>
                </div>
                <div className="web3_service_sec_offers">
                  <svg
                    width="35"
                    height="36"
                    viewBox="0 0 35 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M0 18C0 13.3587 1.84374 8.90752 5.12563 5.62563C8.40752 2.34374 12.8587 0.5 17.5 0.5C22.1413 0.5 26.5925 2.34374 29.8744 5.62563C33.1563 8.90752 35 13.3587 35 18C35 22.6413 33.1563 27.0925 29.8744 30.3744C26.5925 33.6563 22.1413 35.5 17.5 35.5C12.8587 35.5 8.40752 33.6563 5.12563 30.3744C1.84374 27.0925 0 22.6413 0 18ZM16.5013 25.49L26.5767 12.8947L24.7567 11.4387L16.1653 22.1743L10.08 17.104L8.58667 18.896L16.5013 25.4923V25.49Z"
                      fill="#00C265"
                    />
                  </svg>
                  <h3>Technological Expertise</h3>
                </div>
                <div className="web3_service_sec_offers">
                  <svg
                    width="35"
                    height="36"
                    viewBox="0 0 35 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M0 18C0 13.3587 1.84374 8.90752 5.12563 5.62563C8.40752 2.34374 12.8587 0.5 17.5 0.5C22.1413 0.5 26.5925 2.34374 29.8744 5.62563C33.1563 8.90752 35 13.3587 35 18C35 22.6413 33.1563 27.0925 29.8744 30.3744C26.5925 33.6563 22.1413 35.5 17.5 35.5C12.8587 35.5 8.40752 33.6563 5.12563 30.3744C1.84374 27.0925 0 22.6413 0 18ZM16.5013 25.49L26.5767 12.8947L24.7567 11.4387L16.1653 22.1743L10.08 17.104L8.58667 18.896L16.5013 25.4923V25.49Z"
                      fill="#00C265"
                    />
                  </svg>
                  <h3>On-Time Delivery</h3>
                </div>
                <div className="web3_service_sec_offers">
                  <svg
                    width="35"
                    height="36"
                    viewBox="0 0 35 36"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M0 18C0 13.3587 1.84374 8.90752 5.12563 5.62563C8.40752 2.34374 12.8587 0.5 17.5 0.5C22.1413 0.5 26.5925 2.34374 29.8744 5.62563C33.1563 8.90752 35 13.3587 35 18C35 22.6413 33.1563 27.0925 29.8744 30.3744C26.5925 33.6563 22.1413 35.5 17.5 35.5C12.8587 35.5 8.40752 33.6563 5.12563 30.3744C1.84374 27.0925 0 22.6413 0 18ZM16.5013 25.49L26.5767 12.8947L24.7567 11.4387L16.1653 22.1743L10.08 17.104L8.58667 18.896L16.5013 25.4923V25.49Z"
                      fill="#00C265"
                    />
                  </svg>
                  <h3>Round-the-Clock Support</h3>
                </div>
              </div>
            </Col>
            <Col
              lg={5}
              md={12}
              className="d-md-flex align-items-md-center justify-content-md-center p-md-5"
            >
              <div className="web3_service_offers_man">
                <img
                  src={web3_offers_image}
                  alt="we_offers_man_standing_image"
                />
              </div>
            </Col>
          </Row>
        </div>
      </section>
    </div>
  );
};

export default Web3DevService;
